import React from 'react'
import {FormItem} from '../../FormItem/FormItem'
import {DETAIL} from '../BigCalendar'

type DetailCalendarProps = {
  detail?: DETAIL
}
export const DetailCalendar = (props: DetailCalendarProps) => {
  const {detail} = props
  return (
    <>
      <div className='price-and-term'> GI {'&'} CHNH SCH THEO GI </div>
      <FormItem lableName='Gi p dng (/gi)' value={detail?.PriceByHour} />
      <FormItem lableName='t cc (% n t)' value={detail?.DepositByHour} />
      <FormItem
        lableName='Hnh thc thanh ton'
        value={detail?.PaymentByHour ? 'Chuyn khon' : 'Tin mt'}
      />
      <FormItem lableName='Hy n min ph' value={detail?.FreeCancelByHour} />
      <FormItem lableName='Ph hy n' value={detail?.CancelPriceByHour} />
      <FormItem lableName='Ph vng mt' value={detail?.AbsentPriceByHour} />
      <div className='price-and-term'> GI {'&'} CHNH SCH THEO NGY</div>
      <FormItem lableName='Gi p dng (/ngy)' value={detail?.PriceByDate} />
      <FormItem lableName='t cc' value={detail?.DepositByDate} />
      <FormItem
        lableName='Hnh thc thanh ton'
        value={detail?.PaymentByDate ? 'Chuyn khon' : 'Tin mt'}
      />
      <FormItem lableName='Hy n min ph' value={detail?.FreeCancelByDate} />
      <FormItem lableName='Ph hy n' value={detail?.CancelPriceByDate} />
      <FormItem lableName='Ph vng mt' value={detail?.AbsentPriceByDate} />
    </>
  )
}
